.modal-pane {
  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 10;

  color: var(--beige-color);
  font-size: 25px;

  @media (min-width: 640px) {
    font-size: 40px;
  }
}
